  src: url("Roboto-Bold.fdb9b54a.ttf");
}

@font-face {
  font-family: Roboto-500;
  src: url("Roboto-Medium.91322885.ttf");
}

@font-face {
  font-family: Roboto-400;
  src: url("Roboto-Regular.ca197847.ttf");
}

:root {
  --black: #111;
  --gray-dark: #282828;
  --gray: #595959;
  --gray-light: #b7b7b7;
  --white: #fff;
  --silver: #f8f8f8;
  --orange: #f87719;
  --gradient-1: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --gradient-2: linear-gradient( 141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91% );
}

body {
  background: var(--black);
  color: var(--white);
  font-family: Roboto-400, sans-serif;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 32px;
  padding-right: 32px;
}

li {
  list-style: none;
}

html {
  scroll-behavior: smooth;
}

ul, ol {
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  color: currentColor;
  text-decoration: none;
}

